  margin-left: 14.5%;
  margin-top: 6%;
  border: solid 2px;
  border-color: #a4a65f;
  margin-right: 14%;
  height: 300px;
}

.skills {
  margin-top: 3em;
  -webkit-justify-content: center;
          justify-content: center;
  border: solid 2px;
  border-color: #a4a65f;
  -webkit-column-count: 3;
          column-count: 3;
  width: 30em;
  height: 28em;
  margin-bottom: 10%;
  margin-left: auto;
  margin-right: auto;
  
}


.items {
  width: 2px;
  display: grid;
  margin-right: auto;
